import { useTranslation } from "react-i18next";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/Income/Income.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
import FormInput from "../../../Ui/Input/FormInput";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import {
  slashDateFormat,
  yearMonthDay,
} from "../../../Utils/Functions/dateUtility";
import PaymentModeType from "../../PaymentModeType/PaymentModeType";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import {
  useStudentAdmissionFeeCardCollect,
  useStudentInstituteCharges,
} from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import { paymentCollectType } from "../../../Constant/StudentProfile/student_profile_constant";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../Ui/Tab/Tile/TileCard";
import { get_initiate_paytmV2 } from "../../../../Decrypt/paytmUpiInitiative";
import PaymentGatewayConfig from "../../../../PaymentGateway/PaymentGatewayConfig";
import { useParams } from "react-router-dom";
const StudentAdmissionCollectFees = ({ onClose, install, admission }) => {
  const { t } = useTranslation();
  const params = useParams();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    amount: "",
    mode: "",
    fee_payment_mode: "",
    fee_payment_amount: "",
    transaction_date: "",
    fee_bank_name: "",
    fee_bank_holder: "",
    fee_utr_reference: "",
    pay_remain: false,
  });
  const [errorField, setErrorField] = useState("");
  const [totalAmount, setTotalAmount] = useState(0);

  // for payment gateway
  const [pgstate, setPgstate] = useState({
    amount: 0,
    type: "Admission",
    isTransaction: false,
    pay_remain: false,
  });

  const { studentInstituteCharges } = useStudentInstituteCharges({
    data: {
      id: install?.instituteId,
    },
    skip: !install?.instituteId,
  });

  const [studentAdmissionFeeCardCollect] = useStudentAdmissionFeeCardCollect();

  useEffect(() => {
    if (install?.remainAmount) {
      setState((prev) => ({
        ...prev,
        amount: +install?.remainAmount,
        fee_payment_amount: +install?.remainAmount,
      }));
      setPgstate({
        amount: +install?.remainAmount ?? 0,
        type: "Admission",
        isTransaction: false,
        pay_remain: false,
      });
    }
  }, [install?.remainAmount]);
  const onInputAmount = (e) => {
    setState((pre) => ({
      ...pre,
      amount: +e.target.value,
      fee_payment_amount: +e.target.value,
    }));
    setPgstate((prev) => ({
      ...prev,
      amount: +e.target.value,
    }));
  };

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTransactionDate = (val) => {
    setState((prev) => ({
      ...prev,
      transaction_date: slashDateFormat(val),
    }));
  };

  const onModeChange = (val) => {
    setState((prev) => ({
      ...prev,
      mode: "Online",
      fee_payment_mode: val,
    }));
  };
  const onCollectType = (val) => {
    setState((prev) => ({
      ...prev,
      pay_remain: val,
    }));
    let dt = 0;

    if (val) {
      let t = 0;
      for (let rm of admission?.applicable_card?.remaining_array)
        if (rm?.status !== "Paid") t += rm?.remainAmount;
      setTotalAmount(t);
      dt = t;
    }
    if (dt > 0) {
      setPgstate((prev) => ({
        ...prev,
        pay_remain: val,
        amount: dt,
      }));
    } else {
      setPgstate((prev) => ({
        ...prev,
        pay_remain: val,
      }));
    }
  };
  const onCollectFees = () => {
    let validation = "";
    if (state.fee_payment_mode === "Unrecovered/Exemption")
      validation = requiredField(state, [
        "amount",
        "mode",
        "fee_payment_amount",
        "fee_bank_name",
        "fee_bank_holder",
        "fee_utr_reference",
        "pay_remain",
      ]);
    else
      validation = requiredField(state, [
        "mode",
        "fee_payment_amount",
        "fee_bank_name",
        "fee_bank_holder",
        "fee_utr_reference",
        "pay_remain",
      ]);
    if (existingRequiredField(validation)) {
      if (state.pay_remain && state.amount < totalAmount) {
        setErrorField({
          rest_issue: "Amount should not be less than total remaining fees",
        });
      } else {
        if (
          !state.pay_remain &&
          state.amount > install?.remainAmount &&
          +admission?.fee_structure?.total_installments > 1
        ) {
          setErrorField({
            rest_issue: "Amount should not be greater than remaining fees",
          });
        } else {
          if (admission?.student?._id && install?.appId && install?._id) {
            setDisabled((pre) => !pre);
            studentAdmissionFeeCardCollect({
              aid: admission?.appId?.admissionAdmin?._id,
              sid: admission?.student?._id,
              appId: install?.appId,
              payment: {
                card_id: admission?.applicable_card?._id,
                rid: admission?._id,
                type: install?.installmentValue,
                raid: install?._id,
                nsid: install?._id,
                amount: state?.amount ?? 0,
                mode:
                  state?.fee_payment_mode === "Cash"
                    ? "Offline"
                    : state?.mode ?? "",
                fee_payment_mode:
                  state?.fee_payment_mode === "Cash"
                    ? "By Cash"
                    : state?.fee_payment_mode,
                fee_payment_amount: state?.fee_payment_amount ?? 0,
                transaction_date: yearMonthDay(state.transaction_date) ?? "",
                fee_bank_name: state?.fee_bank_name ?? "",
                fee_bank_holder: state?.fee_bank_holder ?? "",
                fee_utr_reference: state?.fee_utr_reference ?? "",
                pay_remain: state.pay_remain,
              },
            })
              .then(() => {
                setDisabled((pre) => !pre);
                onClose();
              })
              .catch({});
          }
        }
      }
    } else {
      setErrorField(validation);
    }
  };

  // console.info(
  //   "applicant",
  //   admission?.student?._id,
  //   install?.appId,
  //   install?._id
  // );

  // for pg related function
  const onCloseGateway = () => {
    setPgstate((prev) => ({
      ...prev,
      isTransaction: false,
    }));
  };
  const onCollectGatewayFees = () => {
    let validation = requiredField({
      amount: pgstate.amount,
    });
    if (existingRequiredField(validation)) {
      if (pgstate.pay_remain && pgstate.amount < totalAmount) {
        setErrorField({
          rest_issue: "Amount should not be less than total remaining fees",
        });
      } else {
        if (
          !pgstate.pay_remain &&
          pgstate.amount > install?.remainAmount &&
          +admission?.fee_structure?.total_installments > 1
        ) {
          setErrorField({
            rest_issue: "Amount should not be greater than remaining fees",
          });
        } else {
          setPgstate((prev) => ({
            ...prev,
            isTransaction: true,
          }));
        }
      }
    } else {
      setErrorField(validation);
    }
  };

  const onPgCheckoutHandler = () => {
    let gatewayCharges = (+pgstate?.amount * 2.1) / 100;
    let gst = (+gatewayCharges * 18) / 100;
    let withGst = gatewayCharges + gst;
    const data = parseInt(pgstate?.amount);
    var paymentData = {
      amount: Math.ceil(data + +withGst.toFixed(2)),
    };
    // console.info("the let proceudre", obj);
    // console.info("the let 2", admission, install);
    // let obj = {
    //   amount: +pgstate?.amount ?? 10,
    //   amount_nocharges: +paymentData.amount ?? 0,
    //   type: "Admission",
    //   paidBy: `${admission?.student?._id}`,
    //   paidTo: `${install?.instituteId}`,
    //   moduleId: `${install?.appId}`,
    //   ad_status_id: null,
    //   payment_installment: install?.installmentValue,
    //   ad_install: null,
    //   month: null,
    //   payment_remain_1: null,
    //   payment_card_type: null,
    //   payment_book_id: null,
    //   name: params.username,
    //   isApk: "web",
    //   payment_card_id: admission?._id ?? null,
    //   payment_remain_fees: pgstate.pay_remain,
    //   charge: {
    //     num_platform_percent:
    //       studentInstituteCharges?.platform_charges?.percentage ?? 0,
    //     num_platform_max:
    //       studentInstituteCharges?.platform_charges?.maximum ?? 0,
    //     num_trans_pecent:
    //       studentInstituteCharges?.transaction_charges?.percentage ?? 0,
    //     num_trans_max:
    //       studentInstituteCharges?.transaction_charges?.maximum ?? 0,
    //     num_app_max: studentInstituteCharges?.application_charges ?? 0,
    //   },
    // };

    get_initiate_paytmV2({
      amount: +pgstate?.amount ?? 10,
      amount_nocharges: +paymentData.amount ?? 0,
      type: "Admission",
      paidBy: `${admission?.student?._id}`,
      paidTo: `${install?.instituteId}`,
      moduleId: `${install?.appId}`,
      ad_status_id: null,
      payment_installment: install?.installmentValue,
      ad_install: null,
      month: null,
      payment_remain_1: null,
      payment_card_type: null,
      payment_book_id: null,
      name: params.username,
      isApk: "web",
      payment_card_id: admission?._id ?? null,
      payment_remain_fees: pgstate.pay_remain,
      charge: {
        num_platform_percent:
          studentInstituteCharges?.platform_charges?.percentage ?? 0,
        num_platform_max:
          studentInstituteCharges?.platform_charges?.maximum ?? 0,
        num_trans_pecent:
          studentInstituteCharges?.transaction_charges?.percentage ?? 0,
        num_trans_max:
          studentInstituteCharges?.transaction_charges?.maximum ?? 0,
        num_app_max: studentInstituteCharges?.application_charges ?? 0,
      },
      nsid: install?._id,
    })
      .then((data) => {
        // console.info("ldskjfnsbkjasdnf,ms", data);
        window.onScriptLoad(
          data?.response?.body?.txnToken,
          data?.order,
          data?.amount
        );
      })
      .catch((e) => {
        console.info(e);
      });
  };

  return (
    <>
      {pgstate.isTransaction ? (
        <PaymentGatewayConfig
          onClose={onClose}
          allContent={{
            ...pgstate,
            platform_charges:
              studentInstituteCharges?.platform_charges?.percentage,
          }}
          checkoutAdmissionHandler={onPgCheckoutHandler}
          isOptimzed
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("collect_fees")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <div className={style.admission_fees_row}>
                <h6>{t("applicable_amount")} :</h6>
                <h6>
                  {t("rs_only")}{" "}
                  {admission?.applicable_card?.applicable_fee ?? 0}
                </h6>
              </div>
              <div className={style.admission_fees_row}>
                <h6>
                  {t(
                    !state.pay_remain
                      ? "remaining_fees"
                      : "amount_remain_installment"
                  )}{" "}
                  :
                </h6>
                <h6>
                  {t("rs_only")}{" "}
                  {state.pay_remain ? totalAmount : install?.remainAmount ?? 0}
                </h6>
              </div>
              {admission?.fee_structure?.total_installments ? (
                +admission?.fee_structure?.total_installments > 1 ? (
                  <TileWrapper label="pay_rest_remaining_fees">
                    {paymentCollectType?.map((tile) => (
                      <TileCard
                        key={tile?.id}
                        tile={tile}
                        onTile={onCollectType}
                        activeTile={state.pay_remain}
                      />
                    ))}
                  </TileWrapper>
                ) : null
              ) : null}
              <FormInput
                label={t("amount")}
                placeholder={t("amoute_details_collect")}
                name={"amount"}
                value={state.amount}
                type={"tel"}
                onChange={onInputAmount}
                errorShow={errorField["amount"]}
                msg={errorField["rest_issue"]}
              />
              <PaymentModeType
                paymentHeading={t("payment_type_mode")}
                selectLabel={t("mode_placeholder")}
                onClick={onModeChange}
                errorShow={errorField["fee_payment_mode"]}
                defalutValue={state.fee_payment_mode}
                selectedValue={t("please_select_payment_mode")}
                isAuthority="admissionStudentProfileCollectModePg"
                customLabelStyle={{
                  marginTop: "0",
                }}
              />
              {state.fee_payment_mode === "Payment Gateway" ? (
                <CreateButton label="collect" onAction={onCollectGatewayFees} />
              ) : (
                <>
                  <FormCalendarInput
                    label={t("select_date_of_transaction")}
                    placeholder={t("please_select_date")}
                    name={"transaction_date"}
                    value={state.transaction_date}
                    onChange={onTransactionDate}
                    errorShow={errorField["transaction_date"]}
                  />
                  {state.fee_payment_mode === "Cash" ? null : (
                    <>
                      <FormInput
                        label={t("bank_name_collect")}
                        placeholder={t("please_enter_bank_name")}
                        name={"fee_bank_name"}
                        value={state.fee_bank_name}
                        type={"text"}
                        onChange={onInputChange}
                      />
                      <FormInput
                        label={t("banke_holder_name")}
                        placeholder={t("please_enter_name_of_bank_holder")}
                        name={"fee_bank_holder"}
                        value={state.fee_bank_holder}
                        type={"text"}
                        onChange={onInputChange}
                      />
                      <FormInput
                        label={t("cheque_dd_neft")}
                        placeholder={t("please_enter_utr_ref_number")}
                        name={"fee_utr_reference"}
                        value={state.fee_utr_reference}
                        type={"text"}
                        onChange={onInputChange}
                      />
                    </>
                  )}
                  <CreateButton label="collect" onAction={onCollectFees} />
                </>
              )}
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default StudentAdmissionCollectFees;
